import { useCallback, useEffect, useState } from "react";
import { useDepartmentAllMouList } from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import Loading from "../../../../Loader/Loading";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import AddDepartmentSiteMou from "./AddDepartmentSiteMou";
import CreateButton from "../../../../Ui/Button/CreateButton";
import style from "../../../FinanceManager/FinanceManager.module.css";
import DepartmentSiteMouCard from "./DepartmentSiteMouCard";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../../Loader/QvipleLoader";
const DepartmentSiteMou = ({ did }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const {
    departmentAllMouList,
    departmentAllMouListLoading,
    departmentAllMouListRefetch,
  } = useDepartmentAllMouList({
    data: {
      did: did,
      page: page,
      limit: 10,
      search: search,
      batch: "",
    },
    skip: !did,
  });

  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (departmentAllMouList?.list?.length) {
      let obj = nextPage(departmentAllMouList?.list?.length);
      setState(() => obj);
    }
  }, [departmentAllMouList?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    departmentAllMouListRefetch();
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("mou_collabration")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
              <CreateButton
                label="add"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.3rem",
          }}
        >
          {departmentAllMouList?.list?.map((pso, index) =>
            departmentAllMouList?.list?.length === index + 1 ? (
              <div key={pso?._id} ref={ref}>
                <DepartmentSiteMouCard
                  pso={pso}
                  did={did}
                  onRefetch={onRefetch}
                />
              </div>
            ) : (
              <DepartmentSiteMouCard
                pso={pso}
                key={pso?._id}
                did={did}
                onRefetch={onRefetch}
              />
            )
          )}

          {departmentAllMouListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            departmentAllMouList?.list?.length === 0 && (
              <EmptyMessage
                title="No data found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            departmentAllMouList?.list?.length === 0 && (
              <EmptyMessage
                title="No data."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>

      {add && (
        <AddDepartmentSiteMou
          onClose={onToggle}
          did={did}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default DepartmentSiteMou;
