import { useTranslation } from "react-i18next";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useState } from "react";

import {
  useDepartmentRemoveMou,
  useDepartmentUpdateMou,
} from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import AddDepartmentSiteMou from "./AddDepartmentSiteMou";
const DepartmentSiteMouCardMenu = ({ did, onClose, menuData, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");

  const [departmentRemoveMou] = useDepartmentRemoveMou();
  const [departmentUpdateMou] = useDepartmentUpdateMou();
  const onStepClose = () => {
    setStep("");
  };

  const onDelete = () => {
    if (menuData?._id && did) {
      setDisabled((pre) => !pre);
      departmentRemoveMou({
        did: did,
        bodyParameter: {
          mid: menuData?._id,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onEditAction = (data) => {
    if (menuData?._id && did) {
      onStepClose();
      setDisabled((pre) => !pre);
      departmentUpdateMou({
        did: did,
        bodyParameter: {
          ...data,
          mid: menuData?._id,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <>
      {step === 1 ? (
        <AddDepartmentSiteMou
          isEdit
          did={did}
          onEditAction={onEditAction}
          menuData={menuData}
          onClose={onStepClose}
        />
      ) : (
        <ModalMenu onClose={onClose} disabled={disabled}>
          <h6 onClick={() => setStep(1)}>{t("edit")}</h6>
          <h6 onClick={onDelete}>{t("delete")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default DepartmentSiteMouCardMenu;
