import { useTranslation } from "react-i18next";
import style from "../../../StoreManager/StoreManager.module.css";
import { useState } from "react";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import DepartmentSiteMouCardMenu from "./DepartmentSiteMouCardMenu";
import FormFileView from "../../../../Ui/Input/FormFileView";
const DepartmentSiteMouCard = ({ pso, onRefetch, did }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);
  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div
        className={style.smc_card}
        style={{
          alignItems: "flex-start",
        }}
      >
        <div className={style.smc_card_contatiner}>
          <div className={style.smc_card_contatiner}>
            <div className={style.smc_card_contatiner_inner}>
              <h6>
                {t("org_name")} {": "}
                {pso?.org_name ?? ""}
              </h6>
              <p>
                {t("sr_no_only")} {": "}
                {pso?.srn ?? ""}
              </p>
              <p>
                {t("institution_industry")} {": "}
                {pso?.institution_industry ?? ""}
              </p>
              <p>
                {t("durations")} {": "}
                {pso?.durations}
              </p>
              <p>
                {t("batch")} {": "}
                {pso?.batch?.batchName ?? ""}
              </p>
              <p>
                {t("student_count")} {": "}
                {pso?.student_count}
              </p>
              <p>
                {t("staff_count")} {": "}
                {pso?.staff_count}
              </p>
              {pso?.link ? (
                <p>
                  {t("link")} {": "}
                  <a
                    href={pso?.link}
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{
                      color: "#0942a6",
                    }}
                  >
                    {pso?.link}
                  </a>
                </p>
              ) : null}
              {pso?.attach ? (
                <FormFileView
                  label={`${t("attachment")} :`}
                  value={{
                    key: pso?.attach,
                  }}
                  customFormInputContainer={{
                    marginBlock: "0",
                  }}
                />
              ) : null}
            </div>
          </div>
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onToggle}
        />
      </div>
      {open && (
        <DepartmentSiteMouCardMenu
          onClose={onToggle}
          did={did}
          menuData={pso}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default DepartmentSiteMouCard;
