import { useEffect, useState } from "react";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../Ui/Input/FormInput";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useDepartmentAddMou } from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useTranslation } from "react-i18next";
import style from "../../../FinanceManager/Income/Income.module.css";
import UniversalBatchDorpdown from "../UniversalBatchDorpdown";
import FormFile from "../../../../Ui/Input/FormFile";
import { useFileUpload } from "../../../../../hooks/authentication-api";
const AddDepartmentSiteMou = ({
  onClose,
  onRefetch,
  did,
  onEditAction,
  menuData,
  isEdit,
}) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    srn: "",
    org_name: "",
    institution_industry: "",
    durations: "",
    linkUrl: "",
    attach: "",
    batch: "",
    student_count: "",
    staff_count: "",
  });
  const [departmentAddMou] = useDepartmentAddMou();
  const [fileUpload] = useFileUpload();

  useEffect(() => {
    if (isEdit && menuData) {
      setState({
        srn: menuData?.srn ?? "",
        org_name: menuData?.org_name ?? "",
        institution_industry: menuData?.institution_industry ?? "",
        durations: menuData?.durations ?? "",
        linkUrl: menuData?.link ?? "",
        attach: menuData?.attach ?? "",
        batch:
          {
            u_batch: menuData?.batch,
            id: "bsjdngbdsmngdfsfds",
          } ?? "",
        student_count: menuData?.student_count ?? "",
        staff_count: menuData?.staff_count ?? "",
      });
    }
  }, [isEdit, menuData]);

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onSelectBatch = (val) => {
    setState((prev) => ({
      ...prev,
      batch: val,
    }));
  };
  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            attach: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onAdd = () => {
    let validation = requiredField(state, [
      "linkUrl",
      "attach",
      "org_name",
      "institution_industry",
      "durations",
      "batch",
      "student_count",
      "staff_count",
    ]);
    if (
      existingRequiredField(validation) &&
      did &&
      state?.batch?.u_batch?._id
    ) {
      if (isEdit) {
        onEditAction({
          ...state,
          link: state.linkUrl,
          batch: state?.batch?.u_batch?._id,
          student_count: state?.student_count ? +state?.student_count : 0,
          staff_count: state?.staff_count ? +state?.staff_count : 0,
        });
      } else {
        setDisabled((pre) => !pre);
        departmentAddMou({
          did: did,
          bodyParameter: {
            ...state,
            link: state.linkUrl,
            batch: state?.batch?.u_batch?._id,
            student_count: state?.student_count ? +state?.student_count : 0,
            staff_count: state?.staff_count ? +state?.staff_count : 0,
          },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  //   console.info("fdsfgfsd", did, errorField);

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>
                  {t(
                    isEdit
                      ? "update_department_mou_collabrations"
                      : "add_department_mou_collabrations"
                  )}
                </h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("sr_no_only")}
              placeholder={t("sr_no_placeholder")}
              name={"srn"}
              value={state.srn}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["srn"]}
            />
            <FormInput
              label={t("org_name")}
              placeholder={t("org_name_placeholder")}
              name={"org_name"}
              value={state.org_name}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["org_name"]}
            />
            <FormInput
              label={t("institution_industry")}
              placeholder={t("institution_industry_placeholder")}
              name={"institution_industry"}
              value={state.institution_industry}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["institution_industry"]}
            />
            <FormInput
              label={t("durations")}
              placeholder={t("durations_placeholder")}
              name={"durations"}
              value={state.durations}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["durations"]}
            />
            <FormInput
              label={t("link")}
              placeholder={t("link_placeholder")}
              name={"linkUrl"}
              value={state.linkUrl}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["linkUrl"]}
            />
            <FormFile
              label={t("add_attachment")}
              name={"add_attachment"}
              value={
                state.attach
                  ? {
                      key: state.attach,
                      name: "View",
                    }
                  : ""
              }
              onChange={onFileUpload}
              errorShow={errorField["attach"]}
            />
            <UniversalBatchDorpdown
              onSelect={onSelectBatch}
              departmentId={did}
              errorShow={errorField["batch"]}
              defalutValue={state?.batch?.u_batch?.batchName}
            />
            <FormInput
              label={t("student_count")}
              placeholder={t("student_count_placeholder")}
              name={"student_count"}
              value={state.student_count}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["student_count"]}
            />
            <FormInput
              label={t("staff_count")}
              placeholder={t("staff_count_placeholder")}
              name={"staff_count"}
              value={state.staff_count}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["staff_count"]}
            />
            <CreateButton label={isEdit ? "update" : "add"} onAction={onAdd} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default AddDepartmentSiteMou;
